@import '../../assets/stylesheets/app.scss';

$font-path-light: '../../assets/fonts/Titillium Web/TitilliumWeb-Light';
$font-path-light-italic: '../../assets/fonts/Titillium Web/TitilliumWeb-LightItalic';

$font-path-regular: '../../assets/fonts/Titillium Web/TitilliumWeb-Regular';
$font-path-regular-italic: '../../assets/fonts/Titillium Web/TitilliumWeb-Italic';

$font-path-semi-bold: '../../assets/fonts/Titillium Web/TitilliumWeb-SemiBold';
$font-path-semi-bold-italic: '../../assets/fonts/Titillium Web/TitilliumWeb-SemiBoldItalic';

$font-path-bold: '../../assets/fonts/Titillium Web/TitilliumWeb-Bold';
$font-path-bold-italic: '../../assets/fonts/Titillium Web/TitilliumWeb-BoldItalic';

@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url($font-path-light + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: italic;
  font-weight: 300;
  font-display: swap;
  src: url($font-path-light-italic + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url($font-path-regular + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url($font-path-regular-italic + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url($font-path-semi-bold + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: italic;
  font-weight: 600;
  font-display: swap;
  src: url($font-path-semi-bold-italic + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url($font-path-bold + '.ttf');
}

@font-face {
  font-family: 'Titillium Web';
  font-style: italic;
  font-weight: 700;
  font-display: swap;
  src: url($font-path-bold-italic + '.ttf');
}

.defi-subscription-widget {
  color: #fefefe;
  font-family: 'Titillium Web';

  &__header {
    height: 222px;

    &--community {
      background: var(--defi-widget-community-subscription-gradient);
    }

    &--analyst {
      background: var(--defi-widget-analyst-subscription-gradient);
    }

    &--enterprise {
      background: var(--defi-widget-enterprise-subscription-gradient);
    }
  }

  &__price_card {
    background: rgb(81, 98, 146);
    display: inline-table;
  }

  &__price_card:not(:last-child) {
    margin-right: 16px;
  }

  &__price_col {
    letter-spacing: -0.03em;
    text-transform: uppercase;
    font-style: normal;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1rem 0 2rem;
  }

  &__price_row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: baseline;
    justify-content: center;
    column-gap: 10px;
  }

  &__price {
    font-size: 60px;
    font-weight: 300;
  }

  &__symbol {
    font-size: 32px;
  }

  &__name {
    width: 100%;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 48px;
    display: block;
    text-align: center;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    padding-top: 2rem;
  }

  &__features {
    background: rgba(8, 2, 40, 0.6);
    backdrop-filter: blur(30px);
    padding: 24px 24px 40px;
  }

  &__features-list {
    list-style: none;
    color: #ffffff;
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    margin: 16px 0;

    li {
      line-height: 32px;
      display: flex;
      list-style-type: none;
      align-items: baseline;
      column-gap: 24px;
      margin-bottom: 16px;
    }
  }

  &__feat-detail {
    line-height: 24px;
  }

  &__common-feat {
    font-size: 20px;
    line-height: 28px;
    margin: 0 36px;
    text-align: center;
  }

  &__button {
    margin-top: 3rem;

    button {
      border: 0;
      width: fit-content;
      display: block;
      font-weight: 600;
      font-size: 1rem;
      font-style: normal;
      line-height: 1.5rem;
      text-transform: uppercase;
      color: #ffffff;
      margin: auto;
      padding: 0.5rem 1.5rem;
      cursor: pointer;

      &:disabled {
        background: var(--defi-widget-inactive-button-gradient);
      }
    }
  }

  &__subscribe {
    background: var(--defi-widget-active-button-gradient);
  }

  &__tick {
    width: 18px;
    height: 14px;
  }
}
