:root {
  --nvm-one-widget-background-color: #d6d6d6;
  --nvm-one-widget-border-radius: 1.5rem;
  --nvm-one-widget-text-color: #000000;
  --nvm-one-widget-title-size: 21px;
  --nvm-one-widget-cta-background-color: #000000;
  --nvm-one-widget-cta-text-color: #ffffff;

  --defi-widget-community-subscription-gradient: linear-gradient(
    91.25deg,
    #3448f9 0%,
    #41e0f5 100%
  );
  --defi-widget-analyst-subscription-gradient: linear-gradient(
    104.3deg,
    #55d6be 6.55%,
    #448cca 94.47%
  );
  --defi-widget-enterprise-subscription-gradient: linear-gradient(
    91.25deg,
    #3834f9 0%,
    #a97dfb 100%
  );
  --defi-widget-active-button-gradient: linear-gradient(
    92.67deg,
    #7834f9 0%,
    #67cff9 100%
  );
  --defi-widget-inactive-button-gradient: linear-gradient(
    92.67deg,
    #535058 0%,
    #adb6ba 100%
  );
}

html,
body {
  padding: 0;
  margin: 0;
}
